footer{
    background:var(--color-primary);
    padding: 3rem 0 4rem;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color: var(--color-bg);
}

.footer_logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 1.5rem;
}

.footer_copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* =================MEDIA QUERIES (SMALL DEVICES) ============== */
@media screen and (max-width: 600px){
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }
}