.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about_me{
    width: 100%;
    height: 55%;
    /* height: 32vh; */
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent, var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about_me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about_me-image:hover{
    transform: rotate(0deg);
}

.about_cards{
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.5rem;

}

.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor:default;
}

.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h5{
    font-size: 0.7rem;
}

.about_card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about_content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}


/* =================MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1024px){
    .about_container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about_me{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 50%;
        height: 50%;
        margin: 2rem auto 0;
    }

    
    .about_btn{
       display: flex;
       justify-content: center;
       
    }

    .flex-item{
        margin: 2rem 0;
    }

    .about_cards{
        margin-top: 0rem;
    }
}

/* =================MEDIA QUERIES (SMALL DEVICES) ============== */
@media screen and (max-width: 600px){
    .about_me{
        width: 65%;
        height: 56%;
        margin: 0 auto 3rem;
    }

    .about_content{
        text-align: center;
    }

    .flex-item{
        margin: 2rem 0;
    }

    .about_cards{
        margin-top: 0rem;
    }

    
}